ul {
  list-style: none;
}

.creator__container {
  border: 1px solid rgba(0, 0, 0, .2);
  margin: 0 auto;
  padding: 28px 16px;
}

.creator-header__container {
  align-items: center;
  display: flex;
}

.creator-header__link {
  color: var(--black-main-color);
  margin-left: 40px;
  font-family: Manrope;
  font-size: 42px;
  font-style: normal;
  font-weight: 700;
  line-height: 57px;
  text-decoration: none;
}

.creator__nav {
  margin-left: auto;
}

.creator-nav__list {
  align-items: center;
  display: flex;
}

.creator-nav__item {
  margin-right: 69px;
  list-style: none;
}

.creator-nav__link {
  color: var(--black-main-color);
  font-size: 18px;
  font-weight: 700;
  line-height: 25px;
  text-decoration: none;
}

.hero {
  padding-top: 50px;
  padding-bottom: 50px;
}

.hero__container {
  max-width: 1200px;
  justify-content: space-around;
  align-items: center;
  column-gap: 30px;
  margin: auto;
  display: flex;
}

.hero-text__block {
  max-width: 500px;
  margin-left: 50px;
  line-height: 55px;
}

.hero__title {
  margin-bottom: 20px;
  font-family: Manrope;
  font-size: 44px;
  font-weight: 600;
}

.hero__text {
  font-family: Manrope;
  font-size: 20px;
}

.developer {
  padding-top: 50px;
  padding-bottom: 100px;
}

.developer__container {
  max-width: 1200px;
  margin: auto;
}

.developer__list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin: auto;
  display: flex;
}

.developer__item {
  width: 100%;
  width: calc(25% - 22.5px);
}

.developer__line {
  position: relative;
  overflow: hidden;
}

.developer__img {
  max-width: 100%;
  height: auto;
  border-radius: 50%;
  margin-bottom: 25px;
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.developer__name {
  text-align: center;
  margin-bottom: 15px;
}

.developer__duties {
  text-align: center;
  color: #a8a8a8;
  margin-bottom: 15px;
}

.developer-social__list {
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 0;
  display: flex;
}

.developer-social__link {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.developer-social__link:hover, .developer-social__link:focus {
  background-color: var(--blue-main-color);
  color: var(--white-main-color);
}

.developer-socials__icon {
  fill: #a8a8a8;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.developer-social__link:hover .developer-socials__icon, .developer-social__link:focus .developer-socials__icon {
  fill: var(--white-main-color);
}

.creators__footer {
  border: 1px solid rgba(0, 0, 0, .2);
  padding: 55px;
}

